import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom';
import { useParams } from 'react-router-dom';

import Spinner from '../../../../components/UI/Spinner/Spinner';
import { getUserById } from '../../../../services';

import { Card } from 'react-bootstrap';

import classes from './User.module.css';

const User = () => {
    const { id } = useParams();
    const [user, setUser] = useState({});
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        getUserById({ id, setUser, setLoading })
    }, [id])
    return (
        loading ?
            <Spinner /> :
            <Card className={classes.User}>
                <Card.Img
                    variant="top"
                    src={
                        user.type === 'admin' ?
                            'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTW4CH2TTTQuYDIpSsN8yIpiCEG6YgBVAkbpQ&usqp=CAU' :
                            user.type === 'mesero' ?
                                'data:image/jpeg;base64,' :
                                user.type === 'chef' ?
                                    'https://media.gettyimages.com/photos/pan-fried-duck-picture-id1081422898?s=612x612' :
                                    'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRh2j_No99ryewMw39sMjVID6QUf342dDk2Bw&usqp=CAU'
                    }
                />
                <Card.Body>
                    <Card.Title style={{ textTransform: 'capitalize' }}>{`${user.type}: ${user.firstName} ${user.lastName}`}</Card.Title>
                    <Card.Text>
                        {`Email: ${user.email}`}
                    </Card.Text>
                </Card.Body>
                <Card.Footer>
                    <small className="text-muted">
                        {`Ultima actualizacin el ${new Date(user.updatedAt).getDate()}/${new Date(user.updatedAt).getMonth() + 1}/${new Date(user.updatedAt).getFullYear()}`}
                        <br />
                        <Link to={`/admin/users/edit/${user._id}`}>Editar</Link>
                    </small>
                </Card.Footer>
            </Card>
    )
}

export default User
